<template>
  <div
    id="selfPassenger"
    v-loading="loading"
    element-loading-background="#f8f8f8"
  >
    <el-row>
      <h2>乘客</h2>
      <el-divider></el-divider>
      <el-card
        style="margin: 10px 0; height: 110px; position: relative"
        shadow="hover"
        v-for="(passenger, index) in passengerList"
        :key="index"
        body-style="padding: 10px"
      >
        <div class="row">
          <i class="el-icon-user-solid"></i>
          <div>
            <div>
              <span style="font-weight: bold; color: #409eff">乘客姓名：</span>
              <span style="font-size: 15px"
                >{{ passenger.passengerName }}
              </span>
            </div>
            <div style="margin-top: 10px">
              <span style="font-weight: bold; color: #409eff"
                >乘客身份证号：</span
              >
              <span style="font-size: 15px">{{ passenger.passengerId }} </span>
            </div>
            <div style="margin-top: 10px">
              <span style="font-weight: bold; color: #409eff">乘客电话：</span>
              <span style="font-size: 15px">{{ passenger.phoneNumber }} </span>
            </div>
          </div>
        </div>

        <el-popconfirm
          title="确定删除该乘客信息？"
          @confirm="deletePassenger(passenger)"
        >
          <el-button
            type="text"
            class="el-icon-close close"
            slot="reference"
          ></el-button>
        </el-popconfirm>
      </el-card>
      <div
        style="
          width: 100%;
          display: flex;
          justify-content: center;
          align-items: center;
        "
      >
        <el-button
          class="el-icon-plus"
          type="text"
          style="margin-top: 10px; font-size: 20px"
          @click="addPassenger"
        >
          添加乘客
        </el-button>
      </div>
    </el-row>

    <el-dialog title="乘客信息" :visible.sync="addDialog" append-to-body>
      <el-form
        label-width="120px"
        label-position="left"
        :model="passengerForm"
        :rules="rules"
        ref="passengerForm"
      >
        <el-form-item label="乘客姓名" prop="passengerName">
          <el-input
            style="width: 60%"
            v-model="passengerForm.passengerName"
          ></el-input>
        </el-form-item>
        <el-form-item label="乘客身份证号" prop="passengerIdCard">
          <el-input
            style="width: 60%"
            v-model="passengerForm.passengerIdCard"
          ></el-input>
        </el-form-item>
        <el-form-item label="乘客电话" prop="phoneNumber">
          <el-input
            style="width: 60%"
            v-model="passengerForm.phoneNumber"
          ></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialog = false">取 消</el-button>
        <el-button type="primary" @click="add">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import webconfig from "@/data/config";
import axios from "axios";
import { mapActions } from "vuex";
export default {
  data() {
    return {
      loading: true,
      passenger: 8,
      addDialog: false,
      passengerList: [],
      passengerForm: {
        passengerName: "",
        passengerIdCard: "",
        phoneNumber: "",
      },
      rules: {
        passengerName: [
          { required: true, message: "请输入乘客姓名", trigger: "blur" },
        ],
        passengerIdCard: [
          { required: true, message: "请输入乘客身份证号", trigger: "blur" },
          {
            pattern: /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/,
            message: "身份证号码格式不正确",
            trigger: "blur",
          },
        ],
        phoneNumber: [
          { required: true, message: "请输入乘客电话", trigger: "blur" },
        ],
      },
    };
  },
  methods: {
    ...mapActions("login", { getInfo: "getInfo" }),
    getPassegers() {
      axios({
        url: `${webconfig.url}/passenger/findPsgByUsrId`,
        headers: { token: localStorage.getItem("token") },
        params: { usrId: this.$store.state.user.userForm.usrId },
      })
        .then((value) => {
          if (value.data.code == 200) {
            console.log(value.data.data);
            this.passengerList = value.data.data;
          } else {
            this.$message({
              message: value.data.message,
              type: "error",
              duration: 1000,
            });
          }
        })
        .catch((error) => {
          this.$message({
            message: error,
            type: "error",
            duration: 1000,
          });
        });
    },
    addPassenger() {
      this.addDialog = true;
    },
    add() {
      this.$refs["passengerForm"].validate((valid) => {
        if (valid) {
          axios({
            url: `${webconfig.url}/passenger/addPassenger`,
            method: "post",
            headers: { token: localStorage.getItem("token") },
            data: {
              usrId: this.$store.state.user.userForm.usrId,
              passengerName: this.passengerForm.passengerName,
              passengerId: this.passengerForm.passengerIdCard,
              phoneNumber: this.passengerForm.phoneNumber,
              passengerType: "飞机",
            },
          })
            .then((value) => {
              if (value.data.code == 200) {
                this.$message({
                  message: value.data.message,
                  type: "success",
                  duration: 1000,
                });
                this.addDialog = false;
                this.getPassegers();
              } else {
                this.$message({
                  message: value.data.message,
                  type: "error",
                  duration: 1000,
                });
              }
            })
            .catch((error) => {
              this.$message({
                message: error,
                type: "error",
                duration: 1000,
              });
            });
        } else {
          this.$message({
            message: "请检查输入是否正确",
            type: "error",
            duration: 1000,
          });
        }
      });
    },
    deletePassenger(row) {
      axios({
        url: `${webconfig.url}/passenger/deletePassenger`,
        method: "delete",
        headers: { token: localStorage.getItem("token") },
        data: {
          usrId: this.$store.state.user.userForm.usrId,
          passengerName: row.passengerName,
          passengerId: row.passengerId,
          phoneNumber: row.phoneNumber,
        },
      })
        .then((value) => {
          if (value.data.code == 200) {
            this.$message({
              message: value.data.message,
              type: "success",
              duration: 1000,
            });
            this.getPassegers();
          } else {
            this.$message({
              message: value.data.message,
              type: "error",
              duration: 1000,
            });
          }
        })
        .catch((error) => {
          this.$message({
            message: error,
            type: "error",
            duration: 1000,
          });
        });
    },
  },
  activated() {
    this.getInfo(this);
    this.getPassegers(this);
    setTimeout(() => {
      this.loading = false;
    }, 1000);
  },
  deactivated() {
    console.log("deactivated");
    this.loading = true;
  },
};
</script>

<style scoped>
#selfPassenger {
  padding: 30px 25px;
}
i {
  margin-right: 10px;
  font-size: 80px;
  color: aquamarine;
  transition: all 0.3s ease-in-out;
}
i:hover {
  color: white;
  background-color: aquamarine;
}
.row {
  display: flex;
  justify-content: start;
  align-items: center;
}
.col {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: start;
}
.close {
  position: absolute;
  top: 0px;
  right: 10px;
  font-size: 20px;
  color: #c0c4cc;
}
.close:hover {
  color: #909399;
}
</style>
